@import url(https://fonts.googleapis.com/css?family=Quicksand|Secular+One&display=swap);
body, html {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a.navbar-brand, a.active {
  color: black; }
  a.navbar-brand:hover, a.active:hover {
    color: #FF5851; }

a.active {
  margin-right: 50px;
  margin-top: 15px;
  padding-top: 35px; }
  a.active:hover {
    text-decoration: none;
    color: #FF5851; }

button.contact-btn {
  background-color: black;
  padding: 5px 25px;
  display: inline-block;
  border-radius: 16px; }
  button.contact-btn:hover {
    background-color: #FF5851;
    border: none; }
  button.contact-btn a {
    color: white; }
    button.contact-btn a:hover {
      text-decoration: none; }
    @media (max-width: 767.98px) {
      button.contact-btn a {
        background-color: white;
        color: black;
        padding: 0;
        border: none;
        border-radius: 0; } }
  @media (max-width: 767.98px) {
    button.contact-btn {
      background-color: white;
      color: black;
      padding: 0;
      border: none;
      border-radius: 0; } }

.navigation {
  background-color: white; }

/****** Header *****/
@media (max-width: 767.98px) {
  p {
    font-size: 17px; } }

body {
  border: 50px solid white;
  background-color: #F8F8F8;
  border-top: none;
  font-family: 'Quicksand', sans-serif; }
  @media (max-width: 767.98px) {
    body {
      border: none; } }

.homepage {
  background-color: #F8F8F8; }

h1, h2 {
  font-family: 'Secular One', sans-serif; }

.header {
  height: 85vh;
  background-color: #F8F8F8;
  position: relative;
  display: flex; }
  @media (max-width: 575.98px) {
    .header {
      height: 90vh; } }

.web-background {
  background-image: url(/static/media/web_background.3b01648c.png);
  background-size: cover; }

.title-box {
  position: absolute;
  display: flex;
  height: 85vh;
  justify-content: center; }

.title {
  position: absolute;
  font-size: 120px;
  font-family: 'Secular One', sans-serif;
  z-index: 1;
  line-height: 120px;
  margin-top: 4%; }
  @media (max-width: 575.98px) {
    .title {
      font-size: 90px; } }

.title-caption {
  position: absolute;
  z-index: 1;
  margin-left: 5%; }

.intro-box {
  display: flex;
  align-items: flex-end; }

.intro {
  font-size: 25px;
  max-width: 400px; }
  @media (max-width: 767.98px) {
    .intro {
      font-size: 17px; } }
  @media (max-width: 575.98px) {
    .intro {
      font-size: 15px; } }

/***** About Me *****/
.about-container {
  margin-top: 5%;
  padding-bottom: 2%; }

.about-text {
  font-size: 115px;
  line-height: 90px; }
  @media (max-width: 767.98px) {
    .about-text {
      font-size: 80px; } }

.about-period {
  color: #FF5851; }

.venn {
  height: 400px;
  width: 400px; }
  @media (max-width: 767.98px) {
    .venn {
      height: 200px;
      width: 200px; } }

.squiggle {
  margin-left: 25px; }

/***** Skills ******/
.skills-text {
  width: 400px;
  line-height: 30px; }
  @media (max-width: 767.98px) {
    .skills-text {
      width: 300px; } }

.bold {
  font-family: 'Secular One', sans-serif; }

.skills-container {
  padding-bottom: 5%;
  padding-right: 0%; }

.skills-period {
  color: #FF5851;
  font-family: 'Secular One', sans-serif;
  font-size: 60px; }

.skills-list {
  background-color: black;
  width: 100%;
  padding: 5%; }

.skills-intro {
  width: 425px; }
  @media (max-width: 767.98px) {
    .skills-intro {
      width: 300px;
      margin-left: 0; } }

li {
  list-style-type: none;
  color: white; }

/*** Work ****/
.work-text {
  font-size: 115px;
  width: 100px;
  line-height: 90px; }
  @media (max-width: 767.98px) {
    .work-text {
      font-size: 80px; } }

.selection {
  display: flex;
  align-items: flex-end; }

.selection-text {
  width: 350px;
  line-height: 30px; }
  @media (max-width: 767.98px) {
    .selection-text {
      width: 300px; } }

.black-period {
  font-family: 'Secular One', sans-serif;
  font-size: 60px; }

/***** Medpius ****/
.period {
  font-size: 35px; }

.medpius {
  height: 360px;
  padding-bottom: 30px;
  background-color: #F5D33E;
  flex-direction: column;
  display: flex;
  justify-content: flex-end; }
  .medpius p {
    color: #F8F8F8;
    opacity: 80%; }
  .medpius .name {
    color: white;
    font-family: 'Secular One', sans-serif;
    margin-bottom: 0; }
  .medpius .info {
    color: #1E7FC6;
    font-family: 'Secular One', sans-serif;
    width: 400px; }
  @media (max-width: 767.98px) {
    .medpius .info {
      width: 300px; } }

/***** CSSA ****/
.cssa {
  height: 360px;
  padding-bottom: 30px;
  background-color: #263477;
  flex-direction: column;
  display: flex;
  justify-content: flex-end; }
  .cssa p {
    color: #F8F8F8;
    opacity: 80%; }
  .cssa .name {
    color: white;
    font-family: 'Secular One', sans-serif;
    margin-bottom: 0; }
  .cssa .info {
    color: #F5D33E;
    font-family: 'Secular One', sans-serif;
    width: 400px; }
  @media (max-width: 767.98px) {
    .cssa .info {
      width: 300px; } }

/***** Jamming ****/
.project-container {
  padding-bottom: 5%; }

.jamming {
  height: 360px;
  padding-bottom: 30px;
  background-color: #D34141;
  flex-direction: column;
  display: flex;
  justify-content: flex-end; }
  .jamming p {
    color: #F8F8F8;
    opacity: 80%; }
  .jamming .name {
    color: white;
    font-family: 'Secular One', sans-serif;
    margin-bottom: 0; }
  .jamming .info {
    color: #C8D0F8;
    font-family: "Secular One", sans-serif;
    width: 400px; }
  @media (max-width: 767.98px) {
    .jamming .info {
      width: 300px; } }

/***** Ravenous ****/
.ravenous {
  height: 360px;
  padding-bottom: 30px;
  background-color: #1E7FC6;
  flex-direction: column;
  display: flex;
  justify-content: flex-end; }
  .ravenous p {
    color: #F8F8F8;
    opacity: 80%; }
  .ravenous .name {
    color: white;
    font-family: "Secular One", sans-serif;
    margin-bottom: 0; }
  .ravenous .info {
    color: #FBA8A3;
    font-family: "Secular One", sans-serif;
    width: 400px; }
  @media (max-width: 767.98px) {
    .ravenous .info {
      width: 300px; } }

/**** Footer ****/
footer {
  padding-bottom: 2%; }
  footer h5 {
    font-family: "Secular One", sans-serif; }
  footer a {
    font-family: "Secular One", sans-serif;
    color: black; }
    footer a:hover {
      color: #FF5851;
      text-decoration: none; }
  footer .box {
    border-right: 1px lightgray solid; }
    @media (max-width: 575.98px) {
      footer .box {
        border-right: none; } }

